import React, { Component } from 'react';
import './Footer.css';

class Footer extends Component {
  render() {
    return (
      <section>
      <span>Footer</span>
      <p>P.S. Это очередной сайт в исполнении... с использованием фреймворка реакт.</p>
      <i style={{background: "green",borderRadius:"27px",padding:"1%",border:"1px solid blue"}}><a href="https://nasobe.ru/">На главную</a></i>
      </section>
    );
  }
}

export default Footer;



